import { Grid } from '@mui/material';

import backg from "../background/bck7-min.jpg"

// ==============================|| AUTH BLUR BACK SVG ||============================== //

const AuthBackground = () => {
    
    return (
        <Grid 
            container 
            spacing={0}
            direction="column"
            alignItems="center"
            justifyContent="center"
            sx={{ 
                minHeight: '100%',
                width:"100%",
                position: 'fixed',
                backgroundColor: 'primary.lingw',
                backgroundImage: `url(${backg})`,
                backgroundRepeat: 'no-repeat', 
                backgroundPosition: 'center 0px',
                backgroundSize: 'cover',
                filter: 'blur(0.4px)', 
                zIndex: -5, 
                bottom: 0,
                scrollBehavior:'smooth',
                
                
            }}
        >
            
        </Grid>
    );
};

export default AuthBackground;