import React from "react";

import { Outlet } from "react-router-dom";



import { Box, Grid } from "@mui/material";
import Footer from "../components/Footer/Footer";
import AuthBackground from "../../assets/background/AutoBackground";
//import Logo from "./Logo/Logo";
//import AuthFooter from "./AuthFooter/AuthFooter";

/*import {
  experimentalStyled,
  useMediaQuery,
  Container,
  Box,
} from "@material-ui/core";*/

const AuthLayout = () => {
  

  return (
    <Box sx={{fontSize: '1rem',}} >
      <Grid
        container
        spacing={0}
        direction='column'
        justifyContent='center'
        sx={{
          minHeight: { xs: 'calc(100vh - 150px)', md: 'calc(100vh - 150px)' },

        }}
      >

        <Grid item sx={12}>

          <Grid
            item
            xs={12}
            container
            justifyContent='center'
            alignItems='center'
            className='box-shadow'
            sx={{ minHeight: { xs: 'calc(100vh - 150px)', md: 'calc(100vh - 150px)' } }}
          >
        <AuthBackground />

            <Grid
              item
              sx={{
                backgroundColor: 'primary.darkling',
                border: '0.1px solid #F2DBF2' ,
                borderRadius: 4,
                maxWidth: { xs: 420, lg: 425 },
                margin: { xs: 2, md: 2 },
                '& > *': {
                  flexGrow: 5,
                  flexBasis: '50%',
                },
              }}
              content={true}
              border={true}
              boxShadow={4}
              className='box-shadow'
            >
              <Box sx={{ p: { xs: 2, sm: 2, md: 2, xl: 2 } }}>
                <Outlet /> {/* or children*/}
              </Box>
            </Grid>
          </Grid>
          
          <Grid item justifyContent='center' xs={12} sx={{ m: 1, mt: 1,}} >
            <Footer />
           {/*<AuthFooter />*/}
          </Grid>

        </Grid>
      </Grid>
    </Box>
  );
};

export default AuthLayout;
